.container{
  text-align: center;
}

.button {
  background-color: #18181880;
  border-radius: 5px;
  padding: 0.5rem;
  margin: 0.1rem;
  color: rgb(236, 236, 236);
  font-size: 1.3rem;
  font-weight: 600;
}

.btn-letter{
  width: 35px;
}

.guess {
  margin-top: 40px;
  margin-bottom: 40px;
}

.letter{
  font-size: 4rem;
  margin-left: 0.4rem;
  margin-right: 0.4rem;
}

.message{
  font-size: 3rem;
  margin-top: 30px;
}

.form {
  margin-top: 30px;
}

.input{
  font-family: inherit;
  border: 0;
  border-bottom: 2px solid rgb(39, 39, 39);
  font-size: 1.3rem;
  color: rgb(15, 15, 15);
  margin: 10px;
  padding: 7px;
  background: transparent;
  transition: border-color 0.2s;
  width: 35px;
}